




















import { Component, Emit, Prop, Vue } from 'vue-property-decorator';

@Component({})
export default class ErrorScreen extends Vue {
  @Prop({ required: true })
  title!: string;
  @Prop({ required: true })
  description!: string;
  @Prop({ required: false, default: '' })
  buttonLabel!: string;

  @Emit()
  dismiss() {}
}
